import React, { useState } from 'react';
import type { NextPage } from 'next';

const Five: NextPage = () => {
  const [buttonType, setButtonType] = useState(0);

  return (
    <div className="self-stretch flex flex-row items-center justify-start gap-[16px] text-lg text-primary-grey-501">
      <button
        onClick={() => setButtonType(0)}
        className={
          'text-gray-2 h-[48px] rounded-full px-5 text-[14px]' +
          (buttonType === 0 ? ' bg-alpha-20' : ' bg-alpha-10')
        }
      >
        Pills
      </button>
      <button
        onClick={() => setButtonType(1)}
        className={
          'text-gray-2 h-[48px] rounded-[12px] px-5 text-[14px]' +
          (buttonType === 1 ? ' bg-alpha-20' : ' bg-alpha-10')
        }
      >
        Rounded
      </button>
      <button
        onClick={() => setButtonType(2)}
        className={
          'text-gray-2 h-[48px] px-5 text-[14px]' +
          (buttonType === 2 ? ' bg-alpha-20' : ' bg-alpha-10')
        }
      >
        Squared
      </button>
    </div>
  );
};

export default Five;
