import React, { useState } from 'react';
import type { NextPage } from 'next';
import One from '../../components/onboardingContent/one';
import Two from '../../components/onboardingContent/two';
import Three from '../../components/onboardingContent/three';
import Four from '../../components/onboardingContent/four';
import Five from '../../components/onboardingContent/five';
import { useRouter } from 'next/router';
import { ButtonLoading } from '@react-page/editor';
import { toast } from 'react-toastify';
import BlackList from '../../constants/BlackList';
import Cookies from 'js-cookie';

const Onboarding: NextPage = () => {
  const router = useRouter();
  const [step, setStep] = useState(0);
  const [siteName, setSiteName] = useState('');
  const [loading, setLoading] = useState(false);
  const [isValidation, setIsValidation] = useState(false);

  const stepDatas = [
    {
      step: 0,
      children: (
        <One
          isValidation={isValidation}
          siteName={siteName}
          setSiteName={setSiteName}
        />
      ),
      title: 'Welcome to your new site',
      description:
        'Before you start building, let’s begin by applying a few settings to bring your concept to life!',
      subtitle: 'What would you like to call your site?',
    },
    {
      step: 1,
      children: <Two />,
      title: 'Choose a theme',
      description:
        'Light websites are more functional. Websites with a lot of media, i.e. videos, galleries, look best on a dark theme.',
      subtitle: 'Light or dark?',
    },
    {
      step: 2,
      children: <Three />,
      title: 'Let’s add some colour',
      description:
        'Select one for now, you can edit these and add custom colours later.',
      subtitle: 'Choose a primary colour',
    },
    {
      step: 3,
      children: <Four />,
      title: 'Fonts say a lot about people!',
      description:
        'They play a vital role in how your website feels. Show some personality! You can edit these at any time. Custom fonts coming soon!',
      subtitle: 'Choose a font family',
    },
    {
      step: 4,
      children: <Five />,
      title: 'Playful, friendly or sharp?',
      description:
        'Buttons play a vital role in how your website feels while exploring, choose wisely. You can edit these at any time.',
      subtitle: 'Choose a button type',
    },
  ];

  const validation = () => {
    switch (step) {
      case 0:
        if (siteName === '') {
          return false;
        } else if (BlackList.includes(siteName.toLowerCase())) {
          toast.error("You can't use this name.");
          return false;
        } else {
          return true;
        }

      case 1:
      case 2:
      case 3:
      case 4:
      default:
        return true;
    }
  };

  const go_head = async () => {
    setLoading(true);
    setIsValidation(true);
    const val_result = validation();
    if (val_result) {
      if (step === 0) {
        createSite();
      } else {
        setStep(step + 1);
        setIsValidation(false);
        setLoading(false);
      }
    } else {
      setLoading(false);
    }
  };

  const go_builder = () => {
    // createSite();
  };

  const createSite = async () => {
    const data = {
      userId: Cookies.get('_id'),
      name: siteName,
    };

    const options = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        authorization: 'bearer ' + Cookies.get('token'),
      },
      body: JSON.stringify(data),
    };

    try {
      await fetch(`${process.env.NEXT_PUBLIC_SERVER_API_URL}/sites`, options)
        .then((res) => res.json())
        .then((data) => {
          if (data.code === 200) {
            router.push({
              pathname: '/builder/[siteId]',
              query: {
                siteId: data.data._id,
              },
            });
          } else {
            setLoading(false);
            toast.error(data.message);
          }
        });
    } catch (error) {
      setLoading(false);
      toast.error('Network Error!');
    }
  };

  return (
    <div style={{ fontFamily: 'RocGrotesk-Regular' }}>
      <div className="hidden md:block relative bg-grey-9001 w-full min-h-[100vh] overflow-hidden text-left text-5xl text-primary-grey-501 font-text-xs-bold1">
        <div className="top-[0px] w-full max-w-[700px] m-[auto] flex flex-col pt-32 box-border gap-[128px] pb-[100px]">
          <div className="self-stretch flex flex-col items-start justify-start gap-[40px]">
            <button
              onClick={() => setStep(step - 1)}
              className={
                'flex flex-row items-center justify-start gap-[6px] opacity-100 hover:opacity-60 cursor-pointer bg-transparent' +
                (step === 0 ? ' hidden' : '')
              }
            >
              <img
                className="relative w-[18px] h-[18px] shrink-0"
                alt=""
                src="/images/vuesaxoutlinearrowleft.svg"
              />
              <div className="flex-1 relative leading-[24px] font-medium text-gray-1 text-[14px]">
                Go back
              </div>
            </button>
            <div className="self-stretch flex flex-col items-start justify-start gap-[12px]">
              <b className="self-stretch relative tracking-[-0.01em] leading-[44px] text-gray-2">
                {stepDatas[step].title}
              </b>
              <div className="self-stretch relative text-xl leading-[28px] text-grey-4001 text-gray-1">
                {stepDatas[step].description}
              </div>
            </div>
            <div className="self-stretch flex flex-col items-start justify-start gap-[24px] text-2xl text-grey-4001">
              <div className="self-stretch relative leading-[30px] font-medium text-gray-1">
                {stepDatas[step].subtitle}
              </div>

              {stepDatas[step].children}
            </div>
          </div>
          <div className="self-stretch flex flex-row items-center justify-start gap-[16px] text-center text-xl">
            <button
              className="rounded-[20px] w-[170px] h-16 flex flex-row py-4 px-8 box-border items-center justify-center gap-[12px] bg-primary-blue opacity-100 hover:opacity-60 text-[18px]"
              onClick={() => go_head()}
            >
              {loading === true ? (
                <ButtonLoading />
              ) : (
                <>
                  <div className="relative leading-[28px] font-medium text-gray-2">
                    Continue
                  </div>
                  <img
                    className="relative w-5 h-5 shrink-0"
                    alt=""
                    src="/images/vuesaxoutlinearrowright2.svg"
                  />
                </>
              )}
            </button>
            {/* <div className="rounded-small h-16 flex flex-row py-4 px-8 box-border items-center justify-center text-grey-4001">
              <div
                onClick={() => go_builder()}
                className="relative leading-[28px] font-medium text-gray-1 opacity-100 hover:opacity-60 cursor-pointer"
              >
                I’ll decide later
              </div>
            </div> */}
          </div>
        </div>
        <div className="absolute top-[0px] left-[0px] w-full flex flex-row p-8 box-border items-center justify-between text-base text-grey-4001">
          <div
            className="opacity-100 hover:opacity-60 cursor-pointer"
            onClick={() => {
              window.location.href = '/home';
            }}
          >
            <img
              className="relative w-[89.78px] h-[32.9px] shrink-0"
              src="/images/frame2.svg"
            />
          </div>
          <div
            onClick={() => {
              window.location.href = '/home';
            }}
            className="relative leading-[24px] font-medium text-gray-1 opacity-100 hover:opacity-60 cursor-pointer"
          >
            Close
          </div>
        </div>
      </div>
      <div className="flex flex-col h-[100vh] md:hidden justify-center gap-[32px] p-[24px]">
        <div className="text-gray-2 text-[48px]">Well sh*t!</div>
        <div className="text-gray-2 text-[16px]">
          The <b>bad news</b> is you’re gonna have to create your website on
          your computer.
          <br />
          <br />
          The <b>good news</b> is mobile website editing is on our development
          roadmap, because that seems like a pretty cool feature, right?
        </div>
        <button
          className="rounded-[20px] h-16 w-fit flex flex-row py-4 px-8 box-border items-center justify-center gap-[12px] bg-primary-blue opacity-100 hover:opacity-60"
          onClick={() => (window.location.href = '/home')}
        >
          <img
            className="relative w-5 h-5 shrink-0 rotate-180"
            src="/images/vuesaxoutlinearrowright2.svg"
          />
          <div className="relative leading-[28px] font-medium text-gray-2">
            Go back
          </div>
        </button>
      </div>
    </div>
  );
};

export default Onboarding;
