import React from 'react';
import type { NextPage } from 'next';
import { getSubDomain } from '@react-page/editor';

type OnePropsType = {
  siteName: string;
  isValidation: boolean;
  setSiteName: (value: string) => void;
};

const One: NextPage<OnePropsType> = ({
  siteName,
  isValidation,
  setSiteName,
}) => {
  return (
    <div className="self-stretch flex flex-col items-start justify-start gap-[16px] text-lg text-primary-grey-501">
      <input
        value={siteName}
        onChange={(e) => setSiteName(e.target.value || '')}
        className={
          'h-[48px] bg-alpha-10 text-gray-1 text-[14px] rounded-lg border-[1px] focus:border-solid focus:outline-none border-white px-[20px] block w-full dark:bg-gray-700 dark:placeholder-gray-400 dark:text-white dark:focus:ring-gray-2 dark:focus:border-blue-500 ' +
          (isValidation === true && siteName === ''
            ? 'border-red border-solid border-[1px]'
            : '')
        }
        placeholder="Please type your site name"
        required
      ></input>
      <div className="self-stretch relative text-base leading-[24px] text-grey-4001 text-gray-1">
        {getSubDomain(siteName)}
      </div>
    </div>
  );
};

export default One;
