import type { NextPage } from 'next';
import React, { useState } from 'react';
import FontList from '../../constants/FontList';

const Four: NextPage = () => {
  const [selectedHeading, setSelectedHeading] = useState(FontList[0].value);
  const [selectedParagraph, setSelectedParagraph] = useState(FontList[0].value);
  const [selectedButton, setSelectedButton] = useState(FontList[0].value);

  return (
    <div>
      <div className="self-stretch flex flex-col items-start justify-start gap-[16px] text-lg text-primary-grey-501">
        <div className="text-gray-2">Headings</div>
        <select
          id="headingText"
          value={selectedHeading}
          onChange={(e) => setSelectedHeading(e.target.value)}
          className="h-[48px] bg-alpha-10 text-gray-2 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
        >
          {FontList.map((item, index) => (
            <option
              key={index}
              value={item.value}
              className="text-gray-2 bg-gray"
            >
              {item.title}
            </option>
          ))}
        </select>
      </div>
      <div className="self-stretch flex flex-col items-start justify-start gap-[16px] text-lg text-primary-grey-501 pt-8">
        <div className="text-gray-2">Paragraphs</div>
        <select
          id="paragraphText"
          value={selectedParagraph}
          onChange={(e) => setSelectedParagraph(e.target.value)}
          className="h-[48px] bg-alpha-10 text-gray-2 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
        >
          {FontList.map((item, index) => (
            <option
              key={index}
              value={item.value}
              className="text-gray-2 bg-gray"
            >
              {item.title}
            </option>
          ))}
        </select>
      </div>
      <div className="self-stretch flex flex-col items-start justify-start gap-[16px] text-lg text-primary-grey-501 pt-8">
        <div className="text-gray-2">Buttons</div>
        <select
          id="buttonText"
          value={selectedButton}
          onChange={(e) => setSelectedButton(e.target.value)}
          className="h-[48px] bg-alpha-10 text-gray-2 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
        >
          {FontList.map((item, index) => (
            <option
              key={index}
              value={item.value}
              className="text-gray-2 bg-gray"
            >
              {item.title}
            </option>
          ))}
        </select>
      </div>
      <div className="self-stretch flex flex-col items-start justify-start gap-[16px] text-lg text-primary-grey-501 pt-8">
        <div className="text-gray-1">Preview</div>
        <div className="flex flex-col p-[16px] gap-[16px] bg-alpha-10 rounded-[16px]">
          <div className={'text-gray-2 text-[24px] ' + selectedHeading}>
            Heading
          </div>
          <div className={'text-gray-2 text-[14px] ' + selectedParagraph}>
            Maecenas cras id feugiat tempus interdum. Amet sit curabitur mauris
            ac at. Ultricies purus vitae suscipit ac vel dis. Arcu velit
            tristique consectetur morbi lectus.{' '}
          </div>
          <button
            className={
              'text-gray w-fit bg-gray-2 h-[40px] rounded-[8px] px-5 text-[14px] ' +
              selectedButton
            }
          >
            Button
          </button>
        </div>
      </div>
    </div>
  );
};

export default Four;
