import React, { useState } from 'react';
import type { NextPage } from 'next';
import { ColorPicker, useColor, toColor } from 'react-color-palette';
import 'react-color-palette/lib/css/styles.css';

function hexToRGB(hex: string) {
  const r = parseInt(hex.substring(1, 3), 16);
  const g = parseInt(hex.substring(3, 5), 16);
  const b = parseInt(hex.substring(5, 7), 16);

  return [r, g, b];
}

function rgbToHex(r: number, g: number, b: number) {
  const rVal = r.toString(16).padStart(2, '0').toUpperCase();
  const gVal = g.toString(16).padStart(2, '0').toUpperCase();
  const bVal = b.toString(16).padStart(2, '0').toUpperCase();
  return toColor('hex', `#${rVal}${gVal}${bVal}`);
}

const Three: NextPage = () => {
  const [selectedColor, setSelectedColor] = useState(0);
  const [open, setOpen] = useState(false);
  const [color, setColor] = useColor('hex', '#FFFFFF');

  const colors = [
    { name: 'Greyscale', code: 'bg-greyscale', hex: '#636769' },
    { name: 'Blue', code: 'bg-blue', hex: '#2E90FA' },
    { name: 'Red', code: 'bg-red', hex: '#F04438' },
    { name: 'Green', code: 'bg-green', hex: '#12B76A' },
    { name: 'Pink', code: 'bg-pink', hex: '#F63D68' },
    { name: 'Yellow', code: 'bg-yellow', hex: '#FDB022' },
    { name: 'Purple', code: 'bg-purple', hex: '#9E77ED' },
    { name: 'Orange', code: 'bg-orange', hex: '#FB6514' },
  ];

  const handleSelectedColor = (id: number) => {
    setSelectedColor(id);
    setColor(toColor('hex', colors[id - 1]?.hex || '#FFFFFF'));
    setOpen(true);
  };

  const handleHexChange = (hex: string) => {
    setColor(toColor('hex', hex));
  };

  const handleRChange = (r: string) => {
    setColor(rgbToHex(Number(r), color.rgb.g, color.rgb.b));
  };

  const handleGChange = (g: string) => {
    setColor(rgbToHex(color.rgb.r, Number(g), color.rgb.b));
  };

  const handleBChange = (b: string) => {
    setColor(rgbToHex(color.rgb.r, color.rgb.g, Number(b)));
  };

  return (
    <div className="self-stretch flex flex-col items-start justify-start gap-[16px] text-lg">
      <div className="self-stretch grid grid-cols-3 items-start justify-start gap-[16px]">
        <button
          className={
            'flex-1 rounded-xl h-12 flex flex-row py-2 pr-4 pl-1 box-border items-center justify-start gap-[12px] cursor-pointer text-primary-grey-501' +
            (selectedColor === 0 ? ' bg-alpha-20' : ' bg-alpha-10')
          }
          onClick={() => handleSelectedColor(0)}
        >
          <div className="rounded-lg bg-alpha-30 w-10 h-10 shrink-0 flex flex-row p-2 box-border items-center justify-center">
            <img
              className="relative w-5 h-5 shrink-0"
              alt=""
              src="/images/vuesaxoutlineaddcircle2.svg"
            />
          </div>
          <div className="flex-1 relative leading-[28px] font-medium text-gray-2 text-start">
            Custom
          </div>
        </button>
        {colors.map((item, index) => (
          <button
            key={index}
            onClick={() => handleSelectedColor(index + 1)}
            className={
              'flex-1 rounded-xl h-12 flex flex-row py-2 pr-4 pl-1 box-border items-center justify-start gap-[12px]' +
              (selectedColor === index + 1 ? ' bg-alpha-20' : ' bg-alpha-10')
            }
          >
            <div
              className={'relative rounded-lg w-10 h-10 shrink-0 ' + item.code}
            />
            <div className="flex-1 relative leading-[28px] font-medium text-gray-1 text-start">
              {item.name}
            </div>
          </button>
        ))}
      </div>
      <div
        className={
          'absolute flex top-0 left-0 w-[100vw] h-[100vh] bg-[#00000099] items-center justify-center z-20 backdrop-blur-[20px] ' +
          (open === true ? '' : 'hidden')
        }
      >
        <div className="flex flex-col bg-[#181A20] gap-[24px] p-[24px] rounded-[12px] w-[360px] z-20">
          <div className="text-gray-2 text-[18px] leading-[28px] font-medium">
            Select colour
          </div>
          <ColorPicker
            width={312}
            height={238}
            color={color}
            onChange={setColor}
            onChangeComplete={(color): void => {
              console.log(`Final color: ${color}!`);
            }}
            hideHEX
            hideRGB
            hideHSV
            dark
          />
          <div className="grid grid-cols-5 gap-[8px]">
            <div className="col-start-1 col-end-3">
              <label className="text-gray-2">Hex</label>
              <input
                value={color.hex}
                onChange={(e) => handleHexChange(e.target.value)}
                className="h-[48px] bg-alpha-10 text-gray-1 text-sm rounded-lg focus:ring-gray-2 focus:border-alpha-20 block w-full p-2.5 dark:bg-gray-700 dark:placeholder-gray-400 dark:text-white dark:focus:ring-gray-2 dark:focus:border-blue-500"
              ></input>
            </div>
            <div className="col-start-3 col-end-4">
              <label className="text-gray-2">R</label>
              <input
                value={`${hexToRGB(color.hex)[0]}`}
                onChange={(e) => handleRChange(e.target.value)}
                className="h-[48px] bg-alpha-10 text-gray-1 text-sm rounded-lg focus:ring-gray-2 focus:border-alpha-20 block w-full p-2.5 dark:bg-gray-700 dark:placeholder-gray-400 dark:text-white dark:focus:ring-gray-2 dark:focus:border-blue-500"
              ></input>
            </div>
            <div className="col-start-4 col-end-5">
              <label className="text-gray-2">G</label>
              <input
                value={`${hexToRGB(color.hex)[1]}`}
                onChange={(e) => handleGChange(e.target.value)}
                className="h-[48px] bg-alpha-10 text-gray-1 text-sm rounded-lg focus:ring-gray-2 focus:border-alpha-20 block w-full p-2.5 dark:bg-gray-700 dark:placeholder-gray-400 dark:text-white dark:focus:ring-gray-2 dark:focus:border-blue-500"
              ></input>
            </div>
            <div className="col-start-5 col-end-6">
              <label className="text-gray-2">B</label>
              <input
                value={`${hexToRGB(color.hex)[2]}`}
                onChange={(e) => handleBChange(e.target.value)}
                className="h-[48px] bg-alpha-10 text-gray-1 text-sm rounded-lg focus:ring-gray-2 focus:border-alpha-20 block w-full p-2.5 dark:bg-gray-700 dark:placeholder-gray-400 dark:text-white dark:focus:ring-gray-2 dark:focus:border-blue-500"
              ></input>
            </div>
          </div>
          <div className="flex flex-col gap-[12px]">
            <button
              onClick={() => setOpen(false)}
              className="text-gray-2 bg-primary-blue h-[40px] rounded-[8px] px-5"
            >
              Confirm
            </button>
            <button
              onClick={() => setOpen(false)}
              className="text-gray-2 bg-alpha-10 h-[40px] rounded-[8px] px-5"
            >
              Cancel
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Three;
