import React, { useState } from 'react';
import type { NextPage } from 'next';
import { ICON_NAMES, SVGIcon } from '@react-page/editor';

const Two: NextPage = () => {
  const [mode, setMode] = useState(0);

  return (
    <div className="self-stretch flex flex-row items-center justify-start gap-[16px] text-lg text-primary-grey-501">
      <button
        onClick={() => setMode(0)}
        className={
          'flex flex-row items-center gap-[5px] text-gray-2 h-[48px] rounded-[8px] px-5 text-[14px]' +
          (mode === 0 ? ' bg-alpha-30' : ' bg-alpha-10')
        }
      >
        <SVGIcon name={ICON_NAMES.DAY} />
        Daylight
      </button>
      <button
        onClick={() => setMode(1)}
        className={
          'flex flex-row items-center gap-[5px] text-gray-2 h-[48px] rounded-[8px] px-5 text-[14px]' +
          (mode === 1 ? ' bg-alpha-30' : ' bg-alpha-10')
        }
      >
        <SVGIcon name={ICON_NAMES.NIGHT} />
        Twilight
      </button>
      <div className="text-gray-1">More themes coming soon</div>
    </div>
  );
};

export default Two;
